import React from 'react';

export default function Sorting({ setSort }) {
  return (
    <div>
      <h2> </h2>
      <select onChange={(e) => setSort(e.target.value)}>
        <option value=""> </option>
        <option value="asc"> </option>
        <option value="desc"> </option>
      </select>
    </div>
  );
}
